.card-icon {
  float: right;
}
.ui.segment.hero-segment {
  padding: 4em 0em;
}
.ui.segment.hero-segment h1 {
  font-size: 4em;
  font-weight: normal;
  margin-bottom: 0;
}
.ui.segment.hero-segment h2 {
  font-size: 1.4em;
  font-weight: normal;
  margin-top: 0.2em;
}
.ui.segment.info-segment,
.ui.segment.final-segment {
  padding: 8em 0;
}
.ui.segment.info-segment h3,
.ui.segment.final-segment h3 {
  font-size: 1.5em;
}
.ui.segment.info-segment h4.with-div,
.ui.segment.final-segment h4.with-div {
  margin: 10em 0em 5em;
}
.ui.segment.info-segment p,
.ui.segment.final-segment p {
  font-size: 1.2em;
}
.ui.segment.div-segment {
  padding: 0;
}
.ui.segment.div-segment .column.grid-content {
  padding: 5em 0;
}
/* Large Screens */
/* Small Screens */
@media only screen and (max-width: 800px) {
  .ui.segment.hero-segment h1 {
    font-size: 2.2em;
  }
  .ui.segment.hero-segment h2 {
    font-size: 1.1em;
  }
}
/* Tiny Screens */
@media only screen and (max-width: 320px) {
  .ui.segment.hero-segment h1 {
    font-size: 1.8em;
  }
  .ui.segment.hero-segment h2 {
    font-size: 1em;
  }
}
